import React, { useState, useEffect } from 'react';
import EmptyPoster from '../../img/empty_poster.jpg';
import EmptyActorBig from '../../img/empty_actor_b.jpg';
import {
  ItemWrapper,
  Item,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  GoBack,
  Sec,
  SeeMore,
  PhotoGallery,
  CloseButton,
  OpenFull,
  PersonIMG,
} from '../styles/searchstyles';
import Grid from '@material-ui/core/Grid';
import Grow from '@material-ui/core/Grow';
import CloseIcon from '@material-ui/icons/Close';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import ImdbIcon from '../../img/imdb_icon.png';

function Details(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [isLoaded3, setIsLoaded3] = useState(false);
  const [isLoaded4, setIsLoaded4] = useState(false);
  const [actor, setActor] = useState('');
  const [imagesCount, setImagesCount] = useState(3);
  const [personImages, setPersonImages] = useState('');
  const [credits, setCredits] = useState('');
  const [moviesCount, setMoviesCount] = useState(18);
  const [tvCount, setTvCount] = useState(18);
  const [socialLinks, setSocialLinks] = useState('');
  const [galery, setGalery] = useState(false);
  const [modalImg, setModalImg] = useState(false);
  const api_key = '58666352553834b5820ef436680fb2e5';
  const person_id = props.match.params.id;

  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPoster} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActorBig} />;
      }
    }
  };
  const handleMore = (type) => {
    if (type === 'movies') {
      if (moviesCount === 1000) setMoviesCount(18);
      else setMoviesCount(1000);
    } else if (type === 'tv') {
      if (tvCount === 1000) setTvCount(18);
      else setTvCount(1000);
    }
  };
  const handleMoreImages = () => {
    if (imagesCount === 1000) setImagesCount(3);
    else setImagesCount(1000);
  };
  const handleOpen = (src) => {
    setModalImg(src);
    setGalery(true);
  };
  const handleClose = () => {
    setGalery(false);
  };
  const getGender = (g) => {
    if (g === 1) return 'Kobieta';
    else if (g === 2) return 'Mczyzna';
    else return 'Brak danych';
  };
  useEffect(() => {
    fetch(`https://api.themoviedb.org/3/person/${person_id}?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setActor(result);
          setIsLoaded(true);
          if (result.name) {
            document.title = `Aktor: ${result.name}`;
          } else {
            document.title = 'InfCinema';
          }
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/person/${person_id}/combined_credits?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setCredits(result);
          setIsLoaded2(true);
        },
        (error) => {
          setIsLoaded2(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/person/${person_id}/images?api_key=${api_key}`)
      .then((res) => res.json())
      .then(
        (result) => {
          setPersonImages(result);
          setIsLoaded3(true);
        },
        (error) => {
          setIsLoaded3(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/person/${person_id}/external_ids?api_key=${api_key}`)
      .then((res) => res.json())
      .then(
        (result) => {
          setSocialLinks(result);
          setIsLoaded4(true);
        },
        (error) => {
          setIsLoaded4(true);
          setError(error);
        }
      );
    window.scrollTo(0, 0);
  }, []);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded | !isLoaded2) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        <PhotoGallery maxWidth='xs' fullWidth={true} open={galery} onClose={handleClose}>
          <img alt='Modal' src={modalImg} style={{ maxWidth: '100%', maxHeight: '100%' }} />
          <CloseButton onClick={handleClose}>
            <CloseIcon fontSize='large' />
          </CloseButton>
          <OpenFull target='_blank' href={modalImg}>
            Otwrz w penej rozdzielczoci
          </OpenFull>
        </PhotoGallery>
        <Grid container spacing={0} style={{ postition: 'relative' }}>
          <GoBack>
            <ArrowBackIcon fontSize='large' onClick={() => (props.history.length > 2 ? props.history.goBack() : props.history.replace(''))} />
          </GoBack>
          <PhotoContainer item xs={12} md={6} lg={4}>
            {havePhoto(actor, 'big')}
            <TitleItem>{actor.name ? actor.name : 'Brak danych'}</TitleItem>
            {isLoaded4 && (
              <div style={{ display: 'flex', justifyContent: 'center' }}>
                {socialLinks.instagram_id && (
                  <a href={`https://www.instagram.com/${socialLinks.instagram_id}`} target='_blank' rel='noopener noreferrer'>
                    <InstagramIcon style={{ margin: '5px', color: '#E1306C' }} />
                  </a>
                )}
                {socialLinks.facebook_id && (
                  <a href={`https://www.facebook.com/${socialLinks.facebook_id}`} target='_blank' rel='noopener noreferrer'>
                    <FacebookIcon style={{ margin: '5px', color: '#4267B2' }} />
                  </a>
                )}
                {socialLinks.twitter_id && (
                  <a href={`https://www.twitter.com/${socialLinks.twitter_id}`} target='_blank' rel='noopener noreferrer'>
                    <TwitterIcon style={{ margin: '5px', color: '#1da1f2' }} />
                  </a>
                )}
                {socialLinks.imdb_id && (
                  <a href={`https://www.imdb.com/name/${socialLinks.imdb_id}`} target='_blank' rel='noopener noreferrer'>
                    <img alt='imdb' src={ImdbIcon} width='20px' style={{ margin: '5px' }} />
                  </a>
                )}
              </div>
            )}
          </PhotoContainer>
          <InfoContainer item xs={12} md={6} lg={8} xl={8}>
            <MovieInfo>
              Pe: <span>{getGender(actor.gender)}</span>
            </MovieInfo>
            <MovieInfo>
              Data urodzenia: <span>{actor.birthday ? actor.birthday : 'Brak danych'}</span>
            </MovieInfo>
            {actor.deathday && (
              <MovieInfo>
                Data mierci: <span>{actor.deathday ? actor.deathday : 'Brak danych'}</span>
              </MovieInfo>
            )}
            <MovieInfo>
              Wiek:{' '}
              <span>
                {actor.birthday
                  ? Math.abs(new Date((actor.deathday ? new Date(actor.deathday) : new Date()) - new Date(actor.birthday)).getUTCFullYear() - 1970)
                  : 'Brak danych'}
              </span>
            </MovieInfo>
            <MovieInfo>
              Miejsce urodzenia: <span>{actor.place_of_birth ? actor.place_of_birth : 'Brak danych'}</span>
            </MovieInfo>
            {actor.also_known_as && (
              <MovieInfo>
                Rwnie znany/a jako: <span>{actor.also_known_as.length > 0 ? actor.also_known_as.join(', ') : 'Brak danych'}</span>
              </MovieInfo>
            )}
            {isLoaded3 && (
              <MovieInfo>
                {personImages.profiles.filter((item) => item.file_path !== actor.profile_path).length > 0 && 'Fotografie:'}
                <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center', flexWrap: 'wrap' }}>
                  {personImages.profiles
                    .filter((item) => item.file_path !== actor.profile_path)
                    .slice(0, imagesCount)
                    .map((item, index) => (
                      <Grow
                        key={index}
                        onClick={() => handleOpen(`https://image.tmdb.org/t/p/original${item.file_path}`)}
                        in={true}
                        style={{ transformOrigin: '0 0 0', cursor: 'pointer' }}
                        {...{ timeout: 50 * index }}>
                        <PersonIMG src={`https://image.tmdb.org/t/p/w185${item.file_path}`} alt='fotografia' />
                      </Grow>
                    ))}
                </div>
                {personImages.profiles.filter((item) => item.file_path !== actor.profile_path).length > 3 && (
                  <div onClick={() => handleMoreImages()}>{imagesCount > 3 ? <SeeMore>Poka mniej</SeeMore> : <SeeMore>Poka wicej</SeeMore>}</div>
                )}
              </MovieInfo>
            )}
          </InfoContainer>
        </Grid>
        {credits.cast.filter((a) => a.media_type === 'movie').length > 0 && <Sec>Filmy</Sec>}
        <ItemWrapper>
          {credits.cast
            .filter((a) => a.media_type === 'movie')
            .sort((a, b) => {
              if (a.release_date > b.release_date) return -1;
              if (a.release_date < b.release_date) return 1;
              return 0;
            })
            .slice(0, moviesCount)
            .map((item, index) => (
              <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                <Item to={`/${item.media_type}/${item.id}`}>
                  {havePhoto(item)}
                  <div className='tit2'>
                    {item.title} {item.release_date && `(${item.release_date.slice(0, 4)})`}
                    <br />
                    <span className='character'>{item.character}</span>
                  </div>
                </Item>
              </Grow>
            ))}
        </ItemWrapper>
        {credits.cast.filter((a) => a.media_type === 'movie').length > 18 && (
          <div onClick={() => handleMore('movies')}>{moviesCount > 20 ? <SeeMore>Poka mniej</SeeMore> : <SeeMore>Poka wicej</SeeMore>}</div>
        )}
        {credits.cast.filter((a) => a.media_type === 'tv').length > 0 && <Sec>Seriale</Sec>}
        <ItemWrapper>
          {credits.cast
            .filter((a) => a.media_type === 'tv')
            .sort((a, b) => {
              if (a.first_air_date > b.first_air_date) return -1;
              if (a.first_air_date < b.first_air_date) return 1;
              return 0;
            })
            .slice(0, tvCount)
            .map((item, index) => {
              return (
                <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                  <Item to={`/${item.media_type}/${item.id}`}>
                    {havePhoto(item)}
                    <div className='tit2'>
                      {item.name} {item.first_air_date && `(${item.first_air_date.slice(0, 4)})`}
                      <br />
                      <span className='character'>{item.character}</span>
                    </div>
                  </Item>
                </Grow>
              );
            })}
        </ItemWrapper>
        {credits.cast.filter((a) => a.media_type === 'tv').length > 18 && (
          <div onClick={() => handleMore('tv')}>{tvCount > 20 ? <SeeMore>Poka mniej</SeeMore> : <SeeMore>Poka wicej</SeeMore>}</div>
        )}
      </>
    );
  }
}

export default Details;
