import React, { useState, useEffect } from 'react';
import EmptyPosterBig from '../../img/empty_poster_b.jpg';
import EmptyActor from '../../img/empty_actor.jpg';
import EmptyEpisode from '../../img/empty_episode.jpg';
import {
  TitleImg,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  GoBack,
  Sec,
  EpisodeWrapper,
  EpisodeLeftPanel,
  EpisodeTitle,
  EpisodeOverview,
  EpisodeAirDate,
  SeasonPoster,
  EpisodeImg,
  EpisodeImgWrapper,
} from '../styles/searchstyles';
import Grid from '@material-ui/core/Grid';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Grow from '@material-ui/core/Grow';

function Details(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [show, setShow] = useState('');
  const api_key = '58666352553834b5820ef436680fb2e5';
  const season_id = props.match.params.id;
  const season_number = props.match.params.idseason;

  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPosterBig} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActor} />;
      }
    }
  };
  useEffect(() => {
    fetch(`https://api.themoviedb.org/3/tv/${season_id}/season/${season_number}?api_key=${api_key}`)
      .then((res) => res.json())
      .then(
        (result) => {
          result.media_type = 'tv';
          setShow(result);
          setIsLoaded(true);
          if (result.season_number && result.air_date) {
            document.title = `Sezon: ${result.season_number} (${result.air_date.slice(0, 4)})`;
          } else {
            document.title = 'InfCinema';
          }
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );
    window.scrollTo(0, 0);
  }, []);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        <Grid container spacing={0} style={{ postition: 'relative', alignItems: 'flex-start' }}>
          <GoBack>
            <ArrowBackIcon fontSize='large' onClick={() => (props.history.length > 2 ? props.history.goBack() : props.history.replace(''))} />
          </GoBack>
          <PhotoContainer style={{ position: 'relative' }} item xs={12} sm={12} md={4} lg={4}>
            <SeasonPoster>
              {havePhoto(show, 'big')}
              <TitleItem>{show.name}</TitleItem>
              {show.air_date && ` ( ${show.air_date.slice(0, 4)} )`}
            </SeasonPoster>
          </PhotoContainer>
          <InfoContainer item xs={12} sm={12} md={8} xl={8} style={{ marginTop: '10px', background: 'none' }}>
            {show.overview && <Sec style={{ marginTop: '0', marginLeft: '0' }}>Opis Sezonu</Sec>}
            {show.overview}
            <Sec style={{ marginLeft: '0' }}>Odcinki</Sec>
            {show.episodes.map(({ episode_number, season_number, name, overview, still_path, air_date }, index) => (
              <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 110 * (index + 3) }} key={episode_number}>
                <EpisodeWrapper style={{ display: 'flex', justifyContent: 'center' }}>
                  <EpisodeImgWrapper>
                    <EpisodeImg src={still_path ? `https://image.tmdb.org/t/p/w300${still_path}` : EmptyEpisode} />
                  </EpisodeImgWrapper>
                  <EpisodeLeftPanel>
                    <EpisodeTitle>
                      <span>
                        {season_number}x{episode_number < 10 ? `0${episode_number}` : episode_number}
                      </span>{' '}
                      {name}
                    </EpisodeTitle>
                    <EpisodeOverview>{overview}</EpisodeOverview>
                    <EpisodeAirDate>{air_date}</EpisodeAirDate>
                  </EpisodeLeftPanel>
                </EpisodeWrapper>
              </Grow>
            ))}
          </InfoContainer>
        </Grid>
      </>
    );
  }
}

export default Details;
